import React, { Component } from 'react';
import { Row, Col, Input, Button } from 'antd';
import './a.css';
import 'antd/dist/antd.css';
import background1 from '../../assets/background1.jpg';
import background2 from '../../assets/background2.jpg';
import background3 from '../../assets/background3.jpg';
import background4 from '../../assets/background4.jpg';
import background5 from '../../assets/background5.jpg';
import background6 from '../../assets/background6.jpg';
import background7 from '../../assets/background7.jpeg';
import background8 from '../../assets/background8.jpeg';
import { ThemeState } from '../../redux/reducers/themeReducer';
import { ChangeType } from '../../redux/actions/actionTypes';
import ThemeCards from './ThemeCards';

const style = {
  background: '#0092ff',
  margin: '8px 0',
  height: '200px',
};

const arr = [1, 2, 3, 4, 5, 6, 7, 8, 8, 9, 10];
const backgroundList = [
  background1,
  background2,
  background3,
  background4,
  background5,
  background6,
  background7,
  background8,
];

export interface FirstPageEvents {
  // data: [];
  onLoad: () => void;
  onSwitchChange: (type: ChangeType, newState: any, id: string) => void;
  onKeyChange: (key: string) => void;
  onSearch: () => void;
}

export default class All extends Component<FirstPageEvents & ThemeState> {
  componentDidMount() {
    if (this.props.onLoad) {
      this.props.onLoad();
    }
  }
  render() {
    console.log(this.props);
    let arr1;
    if (this.props.data) {
      arr1 = this.props.data.map((item, id) => {
        return (
          <Col className="gutter-row" span={6} key={id}>
            <a href={item.link} target="_blank" className="ach">
              <div
                style={style}
                onClick={() => {
                  this.props.onSwitchChange(
                    ChangeType.visitor,
                    item.visitor + 1,
                    item._id!
                  );
                }}
              >
                <img
                  className="background"
                  src={backgroundList[id % 8]}
                  alt="背景图片"
                />

                <div className="text">主题：{item.project}</div>
                <div className="text">分享人：{item.sharePerson}</div>
                <div className="text">观看量:{item.visitor}</div>
              </div>
            </a>
          </Col>
        );
      });
    }

    return (
      <div>
        <Input
          value={this.props.condition.key}
          onChange={(e) => this.props.onKeyChange(e.target.value)}
          onPressEnter={this.props.onSearch}
        />
        <Button onClick={this.props.onSearch}>搜索</Button>
        <Row gutter={16}>{arr1}</Row>
      </div>
    );
  }
}
